import Print from './printarea.js';
/**
 * @file 
 * `v-print`,
 * `v-print="'#id'"` , .
 */
export default {
    directiveName: 'print',
    bind(el, binding, vnode) {
        let vue = vnode.context;
        let closeBtn = true;
        el.addEventListener('click', (event) => {
            // 
            if (binding.value) {
                localPrint();
            } else {
                window.print();
            }
        });

        const localPrint = () => {
            vue.$nextTick(() => {
                if (closeBtn) {
                    closeBtn = false;
                    let print = new Print({
                        el: binding.value,
                        endCallback() {
                            closeBtn = true;
                        }
                    });
                }
            });
        };
    }
};